// p.235
import React , { useState , useEffect} from 'react'
import useCounter from './useCounter' //   

const MAX_CAPACITY = 10; //  //  

// 1.  
export default function Accommodate( props ) {

    // 1.    [  ,  ,  ]  
    const [ count , increaseCount , decreaseCount] = useCounter(0);
    // 2.    
    const [ isFull , setIsFull  ] = useState( false );

    // 3.
        //  []
        //  : 1. mount[  ] , 2.update   
    useEffect( () => {
        console.log("-----------------------")
        console.log("1   ")
        console.log("isFull : " + isFull )
    } )
    // 3.      true  isFull true  false  
        //     [count]
        //  : 1. mount[  ]  2.update   
    useEffect( () => { console.log("2   "); setIsFull( count >= MAX_CAPACITY ) } , [count] )

        //  : 1. 1. mount[  ] 2. x    3. unmount :   
    useEffect( () => { console.log("3   "); setIsFull( count >= MAX_CAPACITY ) } , [] )

    // 4.   
    return (
        <div style={{ padding:16 }}>
            <p>  { count }  . </p>
            <button onClick={ increaseCount } disabled = {isFull} ></button>
            <button onClick={ decreaseCount } ></button>
            { isFull && <p style={{ color:"red" }}>  .</p>  }
        </div>
    );
}