import React , { useState , useEffect } from 'react'
import axios from 'axios'
import Pagination from 'react-js-pagination' // npm i react-js-pagination 

export default function BoardList(){
    const [ pageInfo , setPageInfo ] = useState({ bcno : 0 , page : 1 , key : "" , keyword:""  }) // 1.    state
    const [ pageDto , setPageDto ] = useState( { list : [] } )                                // 1.   state
    const [ bcategory , setBcategoryList ] = useState( [ ] )                                      // 1.  
                                                    // [ ] : array/list     {  } : object/dto
    // ------------------------------  1.   -------------------------------------- //
        function getboardlist( ){ // 2. server : pageInfo  => pageDto  [  : 1.  2. 3. 4.   --->   ]
            axios   .post( "/board/boardlist" ,  pageInfo )
                    .then( res => {  console.log( res.data );  setPageDto( res.data );  } ).catch( err => { console.log( err ); } )
        }
        useEffect( getboardlist , [ pageInfo ] )  // 3.    *** pageInfo  

    // ------------------------------  2. -------------------------------------- //
        function getBcategory(){ //   
            axios.get("/board/bcategorylist")
                .then( res => { setBcategoryList( res.data ); } ) .catch( err => { console.log( err); } )
        }
        useEffect( getBcategory , [ ] ) // mount , unmount

    //   
        const onCategory = ( bcno ) =>{ setPageInfo( { bcno : bcno , page : 1 , key : "" , keyword:""  } )  }

    // ------------------------------  3.  -------------------------------------- //
        const onPage = ( page ) =>{
                setPageInfo(
                    {   bcno : pageInfo.bcno ,          //  
                        page : page ,
                        key : pageInfo.key ,            //   
                        keyword : pageInfo.keyword  }   //   
                )
            }
    // ------------------------------  4.    -------------------------------------- //
        const onSearch = () => {
            setPageInfo(
                {   bcno : pageInfo.bcno ,  //   [   : pageInfo.bcno ]
                    page : 1 ,              //    [ 1 ]
                    key : document.querySelector('.key').value ,    //  
                    keyword: document.querySelector('.keyword').value  } //  
            )
        }
    return (
        <div>
            <a href="/board/write">[]</a>
            <h1>    </h1>

            <div className="bcategorybox">
                 <button type="button" onClick = { ()=> onCategory( 0 ) }>  </button>

                {
                    bcategory.map( (c) => {
                        return (
                            <button type="button" onClick = { ()=> onCategory( c.bcno ) }>{c.bcname}</button>
                        )
                    })
                }
            </div>

            <table className="btable">
                {
                    pageDto.list.map( ( b ) => {
                        return (
                            <tr>
                                <td> { b.bno } </td>
                                <td> { b.btitle } </td>
                                <td> { b.memail } </td>
                                <td> { b.memail } </td>
                                <td> { b.bview } </td>
                            </tr>
                        )
                    } )
                }
            </table>

            <Pagination
                activePage={ pageInfo.page  }
                itemsCountPerPage = { 3 }
                totalItemsCount = { pageDto.totalBoards }
                pageRangeDisplayed = { 5 }
                onChange={ onPage }
            />
            <div className="searchBox">
                <select className="key">
                    <option value="btitle"></option>
                    <option value="bcontent"></option>
                </select>
                <input type="text" className="keyword" />
                <button type="button" onClick={ onSearch }>  </button>
            </div>

        </div>
    );
}


/*

	1. useState	:  
		const [  , set ] = useState(  )
	2. useEffect	:    
		1. useEffect(   )		: mount , update , unmount
		2. useEffect(  , []  )		: mount  , unmount
		3. useEffect(  , []  )	: mount  , unmount ,   
*/

/*
  API
	1. 
		npm i react-js-pagination
	2.  
		import Pagination from 'react-js-pagination'
	3.  
		<Pagination
            activePage={    }
            itemsCountPerPage = {    }
            totalItemsCount = {    }
            pageRangeDisplayed = {      }
            onChange={   [     ] }
        />
		------ 
        <Pagination
            activePage={ pageInfo.page  }
            itemsCountPerPage = { 3 }
            totalItemsCount = { pageDto.totalBoards }
            pageRangeDisplayed = { 5 }
            onChange={ onPage }
        />
*/