import React from 'react'
import Comment from './Comment'

// 1.   [     ]
const comments = [
    {
        name : "",
        comment : "  ",
    },
    {
        name : " 2 ",
        comment : "     ",
    },
    {
        name : " 3",
        comment : "    ",
    },
];

// map vs forEach
// .map( (  ) => {  } )
function CommentList( props ){
    return(
        <div>
            { comments.map( (c)=>{
                return(
                    <Comment name={c.name} comment={c.comment} />
                );
            } ) }
         </div>
    );
}
export default CommentList