// 1. react   
import React from 'react';
// 2.   [   ]
function Clock( props ){   // props :  [  ]
    // {  } : jsx  [ ,[], ]
    return(
        <div>
            <h1>, ! </h1>
            <h2>  : { new Date().toLocaleTimeString() } </h2>
        </div>
    );
}
// 3.  
export default Clock;