import React , { useState , useEffect } from 'react'
import axios from 'axios'
export default function BoardWrite( props ) {

    let bcno = 0;
    const [ category , setCategory ] = useState('');    //  
    const [ categoryList , setCategoryList ] = useState([]);    //    

    // 1.     [  :  []   ]
            const getbcategory = () => {
                                axios
                                    .get("/board/bcategorylist")
                                    .then( res => { setCategoryList( res.data ); console.log( res)  } )
                                    .catch( err => { console.log( err); } )
                                }
    // useEffect
            useEffect( getbcategory , [] ); //  mount , unmount

    // 2.     [  :     ]
            const setbcategory = () => {
                                if( category == '' ){ alert("  "); return; }
                                axios
                                    .post( "/board/setbcategory" ,  { bcname : category } )
                                    .then( res => {
                                        if( res.data == true ) { alert(" "); console.log( res ) }
                                        else{ alert(" ");  }
                                     })
                                    .catch( err => { console.log( err); } )
                                }

    //  
            const setboard = () => {
                            if(bcno == 0) { alert(" "); return;}
                            let boardform = document.querySelector('.boardform');

                            let formdata = new FormData(boardform);
                            console.log(formdata)
                            console.log(boardform)
                            formdata.set("bcno" , bcno ); //    
                            axios
                                .post("/board/setboard" , formdata , { headers: { 'Content-Type': 'multipart/form-data'  } }  )
                                .then( res => {
                                               console.log(res.data);
                                               console.log(res);
                                               if(res.data == true) { alert('  ')}
                                               else                 { alert('  ')}
                                               } )
                                .catch( err => { console.log( err); } )
                        }

    return(
        <div>
            <h1>    </h1>
              <input type="text" value = {category} onChange={ (e)=> { setCategory(e.target.value ) }  } />
                <button type="button" onClick={ setbcategory }>  </button>
                <div name="bcategorybox" ></div>
                <div className="bcategorybox">
                    {
                        categoryList.map( (c) => {
                            return( <button key={c.bcno } type="button" onClick={ ()=>{ bcno = c.bcno; alert("bcno:::"+bcno)} } > {c.bcname} </button>)
                        })
                    }
                </div>

                <form className="boardform">
                     : <input type="text" name="btitle"/> <br/>
                     : <input type="text" name="bcontent"/> <br/>
                    : <input type="file"  name="bfile"/> <br/>
                    <button type="button" onClick={ setboard }>  </button>
                </form>
        </div>
    );
}