// 1. p.234    
import React , { useState } from 'react';

// 2.   [ use   ]
export default function useCounter( value ){
    // 3. useState 
        // 'count'    , setCount   
        // count    setCount  
    const [ count , setCount ] = useState( value );
    //  [ 1. count     2.count   ]
        // Math.max( 1 , 2 ) :     
    //  =  
    const increaseCount = ()=> setCount( (count) => count+1 ) ;   //   + 1
    const decreaseCount = ()=> setCount( (count) => Math.max(count-1 , 0 ) ) //   - 1 ,  0   

    return [ count , increaseCount , decreaseCount ];   //  
}
