import React from 'react';

// 2.   [  ]
function Book( props ) { // : props [] !!! : 

    // --  [   ]  ajax vs fetch  -- //
    fetch("/member/list")  //  url
        .then( (response) =>  response.json() ) //  
        .then( (data) => console.log( data) );  // 
        // CORS 
    // -------------------------------//
    return (
        <div>
            <h1> {`   ${ props.name } .`} </h1>
            <h1>    { props.name } . </h1>
            <h2>    { props.numOfPage }   . </h2>
        </div>
    ) // return end
} //  end

// 3.   
export default Book